<template>
    <div>
        <order-nav></order-nav>
        <order-con ref="child"></order-con>
    </div>
</template>
<script>
    import orderNav from './children/headerNav.vue';
    import orderCon from './whole.vue';
    export default {
        name : 'order',
        data(){
            return {
                scrollWatch:true
            }
        },
        beforeRouteLeave(to, from, next) {
            let position = this.getChild(); //记录离开页面的位置
            this.$store.state.changeHomeScrollY = position; //离开路由时把位置存起来
            this.$store.state.listData = this.$store.state.order;
            this.$store.state.listInfo = {
                order_status: this.$store.state.status
            };
			next()
        },
        methods:{
            getChild(){
                return this.$refs.child.$el.scrollTop;
            }
        },
        mounted(){
            this.getChild();
        },
        destroyed(){
            this.scrollWatch = false;
        },
        components:{
            orderNav,
            orderCon
        }
    }
</script>


